import React, { Component } from "react";

import "./index.scss";

import Head from "&/components/head";

import { connect } from "react-redux"

import { WingBlank, SearchBar, WhiteSpace } from "antd-mobile"
import { getLdx } from "../../actions";


@connect(
    state => (
        {
            ldxList:state.getIn(['data','ldxList']),
        }
    )
)
class SearchCpt extends Component {


    gotoDetail = (id,name) => {
        this.props.history.push("/detail/"+id+"?name="+name);
    }


    todoSearch = (keyword) => {
        console.log(keyword);
        const { dispatch } = this.props
        dispatch(getLdx({
            url: "/react/ldx",
            params:{keyword}
        }))
    }

    componentDidMount() {
        const { ldxList, dispatch } = this.props
        if(!ldxList.length>0){
            dispatch(getLdx({
                url:"/react/ldx",
                params:{}
            }))
        }
    }

    render() {
        console.log(this.props);
        const{
            ldxList
        }=this.props
        return (
            <div className="search">
                <Head title="商品搜索" show={true} location={this.props.location}></Head>
                <WingBlank>
                    <SearchBar placeholder="请输入商品名称" maxLength={8}
                        onSubmit={this.todoSearch}
                    />
                    <WhiteSpace />
                </WingBlank>
                    <ul className="ldx bounceInUp">
                        {
                            ldxList.map((l, i) => {
                                return (
                                    <li onClick={()=>this.gotoDetail(l._id,l.name)} key={i}  >
                                        <img src={l.url} alt="" />
                                        <div>{l.name}</div>
                                        <p className="price">
                                            <span style={{color:"red"}}>￥{l.newprice}</span>
                                            <span>{l.oldprice}</span>
                                        </p>
                                    </li>
                                )
                            })
                        }
                    </ul>


            </div>
        )
    }
}

export default SearchCpt